<template>
  <div class="main">
    <hatchet v-for="(hatchet, ind) in hatchets" v-bind:id="ind + 1" v-bind:info="hatchet.info" v-bind:key="ind + 1"/>
  </div>
</template>

<script>
import Hatchet from './Hatchet';
import { sounds, playRandomAudio } from '../utils/index';

export default {
  name: 'Game',
  props: ['hatchets'],
  components: { Hatchet },
  methods: {
    guessed() {
      this.$parent.nextLevel();
    },
    failed() {
      this.$parent.attempts--;
      (this.$parent.attempts === 0) ?
        this.$parent.gameOver() :
        this.$parent.nextLevel();
    },
  },
  mounted() {
    this.$parent.shuffleHatchets();
    this.$parent.resetTimer();
    this.$parent.startTimer();
    setTimeout(function() {
      playRandomAudio(sounds.speech);
    }, 500);
  }
};
</script>



