<template>
  <div class="right">
    <div class="timer">
      <div class="timer__value">{{ seconds }}</div>
      <div class="timer__caption">ВРЕМЯ</div>
    </div>
    <div class="year">Видео игра {{ year }} год.</div>
  </div>
</template>

<script>
export default {
  name: 'Right',
  props: ['seconds', 'year'],
}
</script>



