<template>
  <div class="first-screen">
    <div class="first-screen__credits">ВИДЕО ИГРА: Геннадия Горина</div>
    <div class="first-screen__title"> УГАДАЙ <br> ТОПОР</div>
    <div class="hatchet"></div>
    <span class="next" v-on:click="next()">ДАЛЕЕ &raquo;</span>
  </div>
</template>

<script>
import router from '../router/index';
import { sounds } from '../utils/index';

export default {
  name: 'FirstScreen',
  data: function () {
    return {
      audio: new Audio(sounds.start)
    }
  },
  methods: {
    next() {
      this.audio.pause();
      this.audio.currentTime = 0;
      router.replace({ name: 'Rules' });
    }
  },
  mounted() {
    this.audio.play();
  }
}
</script>



