function playRandomAudio(arr, cb) {
  var totalAudios = arr.length;
  var randAudioIndex = Math.floor(Math.random() * totalAudios);
  var audio = new Audio(arr[randAudioIndex]);

  audio.onloadedmetadata = function () {
    audio.play();

    if (cb) {
      setTimeout(function () {
        return cb();
      }, audio.duration * 1000);
    }
  };
}

