<template>
  <div class="try">
    <div class="hatchet"></div>
    <div class="try__title">Уровень: {{ level }}</div>
    <span class="next" v-on:click="start">ИГРАТЬ &raquo;</span>
  </div >
</template>

<script>
import { sounds } from '../utils/index';
import router from '../router/index';

export default {
  name: 'Level',
  props: ['level'],
  data: function() {
    return {
      audio: new Audio(sounds.attempts[this.level - 1])
    }
  },
  methods: {
    start() {
      this.audio.pause();
      this.audio.currentTime = 0;
      router.replace({ name: 'Game' });
    }
  },
  mounted() {
    this.audio.play();
  }
}
</script>



