<template>
  <div class="hatchet" v-on:click="checkHatchet"></div>
</template>

<script>
import { callTheHatchet } from '../utils/index';

export default {
  name: 'Hatchet',
  props: ['info', 'id'],
  methods: {
    checkHatchet() {
      if (this.info) {
        callTheHatchet(this.id, this.$parent.guessed);
      } else {
        this.$parent.failed()
      }
    },
  },
};
</script>



