<template>
  <div class="container" id="app">
    <info :attempts="attempts"/>
    <router-view :hatchets="hatchets" :level="level"/>
    <right :seconds="seconds" :year="year"/>
  </div>
</template>

<script>
import Info from './components/Info';
import Right from './components/Right';
import router from './router/index';

export default {
  name: 'app',

  data() {
    return {
      seconds: 10,
      year: new Date().getFullYear(),
      timerReference: '',
      isRunning: false,
      level: 1,
      attempts: 3,
      hatchets: [
        { info: true },
        { info: false },
        { info: false },
      ],
    }
  },

  components: { Info, Right },

  methods: {
    decreaseSeconds() {
      this.seconds--;

      if (this.seconds === 0) {
        this.resetTimer();
      }
    },

    startTimer() {
      if (!this.isRunning && this.seconds > 0) {
        this.shuffleHatchets();
        this.timerReference = setInterval(this.decreaseSeconds, 1000);
        this.isRunning = true;
      }
    },

    resetTimer() {
      clearInterval(this.timerReference);
      this.seconds = 10;
      this.isRunning = false;
    },

    stopTimer() {
      clearInterval(this.timerReference);
      this.isRunning = false;
    },

    nextLevel() {
      this.level++;
      this.stopTimer();
      router.push({ name: 'Level' });
    },

    resetGame() {
      this.resetTimer();
      this.level = 1;
      this.attempts = 3;
    },

    gameOver() {
      this.resetTimer();
      router.push({ name: 'GameOver' });
    },

    shuffleHatchets() {
      var guess_array = [
        {info: true },
        {info: false },
        {info: false },
      ];

      for (let i = guess_array.length - 1; i >= 0; i--) {
        let rand_index = Math.floor(Math.random() * (i + 1));
        let temp = guess_array[i];
        guess_array[i] = guess_array[rand_index];
        guess_array[rand_index] = temp;
      }

      this.hatchets = guess_array;
    },
  },
};
</script>


