<template>
  <div class="rules">
    <div class="hatchet"></div>
    <div class="rules__title">правила игры</div>
    <p>угадай топор в<br>течении 10 секунд</p>
    <div class="rules__title rules__title--green">справка</div>
    <p>в игре 10 раундов</p>
    <span class="next" v-on:click="next()">Старт &raquo;</span>
  </div>
</template>

<script>
import router from '../router/index';
import { sounds } from '../utils/index';

export default {
  name: 'Rules',
  data: function () {
    return {
      audio: new Audio(sounds.rules)
    }
  },
  methods: {
    next() {
      this.audio.pause();
      this.audio.currentTime = 0;
      router.replace({ name: 'Level' });
    },
  },
  mounted() {
    this.audio.play();
  }
}
</script>




