export const sounds = {
  start: '/static/audio/start.mp3',
  rules: '/static/audio/rules.mp3',
  attempts: [
    '/static/audio/try1.mp3',
    '/static/audio/try2.mp3',
    '/static/audio/try3.mp3',
    '/static/audio/try4.mp3',
    '/static/audio/try5.mp3',
    '/static/audio/try6.mp3',
    '/static/audio/try7.mp3',
    '/static/audio/try8.mp3',
    '/static/audio/try9.mp3',
    '/static/audio/try10.mp3',
  ],
  speech: [
    '/static/audio/speech1.mp3',
    '/static/audio/speech2.mp3',
    '/static/audio/speech3.mp3',
    '/static/audio/speech4.mp3',
    '/static/audio/speech5.mp3',
    '/static/audio/speech6.mp3',
  ],
  hatchets: {
    first: [
      '/static/audio/hatchet1_1.mp3',
      '/static/audio/hatchet1_2.mp3',
      '/static/audio/hatchet1_3.mp3',
    ],
    second: [
      '/static/audio/hatchet2_1.mp3',
      '/static/audio/hatchet2_2.mp3',
    ],
    third: [
      '/static/audio/hatchet3_1.mp3',
      '/static/audio/hatchet3_2.mp3',
      '/static/audio/hatchet3_3.mp3',
      '/static/audio/hatchet3_4.mp3',
      '/static/audio/hatchet3_5.mp3',
    ]
  }
}

export function playRandomAudio(arr, cb) {
  let totalAudios = arr.length;
  let randAudioIndex = Math.floor(Math.random() * totalAudios);
  let audio = new Audio(arr[randAudioIndex]);
  
  audio.onloadedmetadata = () => {
    audio.play();

    if (cb) {
      setTimeout(() => cb(), audio.duration * 1000);
    }
  }
}

export function callTheHatchet(number, cb) {
  switch (number) {
    case 1:
      playRandomAudio(sounds.hatchets.first, cb);
      break;
    case 2:
      playRandomAudio(sounds.hatchets.second, cb);
      break;
    case 3:
      playRandomAudio(sounds.hatchets.third, cb);
      break;
  }
}


