<template>
  <div class="screen">
    <h1>Вы проиграли!</h1>
    <button v-on:click="restart">Начать заново</button>
  </div >
</template>

<script>
import router from '../router/index';

export default {
  name: 'GameOver',
  methods: {
    restart() {
      this.$parent.resetGame();
      router.replace({ name: 'Level'})
    }
  }
}
</script>

<style scoped>
    .screen {
        width: 660px;
        height: 380px;
        flex: 0 0 auto;
        padding: 20px;
        background-color: #0009a8;
        border: 5px solid #07cd12;
    }

    h1 {
        font-size: 44px;
        color: #07cd12;
    }

    button {
        padding: 0;
        border: none;
        background-color: transparent;
        font-family: inherit;
        font-size: 22px;
        color: yellow;
        text-decoration: underline;
        cursor: pointer;
    }

    button:hover {
      text-decoration: none;
    }
</style>



